@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@300;500;900&display=swap");

* {
  box-sizing: border-box;
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 300;
}

body {
  margin: 0;
  padding: 0;
  background-color: #eff3f7;
  height: 100%;
}

html,
body,
.container {
  height: 100%;
  display: flex;
  justify-content: center;
}

.container {
  height: 100%;
  display: flex;
  justify-content: center;
}

.loader {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 900;
  font-size: 30px;
}

.movies {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 50px;
  padding-top: 70px;
  width: 80%;
}
